@use "../../globals" as *;
@use "../../variables" as *;
@use "../../mixins" as *;

.contact-page {
    background-color: $secondaryColor;
    // background-size: cover;
    width: 100vw;
    height: 100vh;
    // padding: 40px 40px 40px 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    color: white;

    .form-container {
        display: flex;
        flex-direction: row;
        width: 60%;
        justify-content: space-between;
        padding: 0 50px; // align-items: center;
        // justify-content: center;
        @include breakpoint-down(medium) {
            flex-direction: column;
            align-items: center;
        }
        // gap: 20px;
        // max-width: 500px;
        margin: 0 auto;

        form {
            // background-color: #444;
            padding: 20px;
            border-radius: 8px;
            width: 400px;

            @include breakpoint-down(medium) {
                width: 250px;
            }

            .form-heading {
                font-size: 24px;
                color: $primaryColor;
                margin-bottom: 15px;
            }

            label {
                display: block;
                font-size: 16px;
                // max-width: 1000px;
                margin-bottom: 5px;
                color: white;
            }

            input,
            textarea {
                resize: vertical;
                width: 100%;
                padding: 10px;
                margin-bottom: 15px;
                border: 1px solid #ccc;
                border-radius: 4px;
            }

            button {
                background-color: $primaryColor;
                color: white;
                display: block;
                padding: 10px 20px;
                border: none;
                border-radius: 4px;
                cursor: pointer;

                &:hover {
                    background-color: $primaryColor;
                }
            }
        }
    }
}
